import React from "react";
import { useAuth0 } from "@auth0/auth0-react";
import "react-step-progress-bar/styles.css";
import { ProgressBar, Step } from "react-step-progress-bar";

export const HeroBanner = () => {

  const { loginWithRedirect } = useAuth0();

  const handleLogin = async () => {
    await loginWithRedirect({
      appState: {
        returnTo: "/profile",
      },
      authorizationParams: {
        prompt: "login",
      },
    });
  };

  const logo = "https://logos-world.net/wp-content/uploads/2020/05/Houston-Texans-logo.png";

  return (
    <div className="hero-banner hero-banner--aqua-emerald">
      <div className="hero-banner__logo">
        <img className="hero-banner__image" src={logo} alt="React logo" />
      </div>
      <h1 className="hero-banner__headline">Welcome to GameDay</h1>
      <p className="hero-banner__description">
        This is a demo for what a fan will experience on game day
      </p>
      <button className="button__login" onClick={handleLogin}>Login to claim your prize</button>
    </div>
  );
};
